@import "_import";


$ambit-semi: 'ambit-semi';
$ambit-regular: 'ambit-regular';
$rosendale: 'roslindaletext';
$notosanskr: 'Noto Sans KR';
$notosanskrm: 'Noto Sans KR Medium';
$pc-side-pad: 80;
$mo-side-pad: 15rem;
$black-color-transition: all .3s ease-in;
$kv-width: 500;
$kv-height: 680;

// 테크 섹션 타이틀 셋
%tech-title-set {
  font-size: 82rem;
  line-height: 1.18;
  font-family: $ambit-semi;
  em {
    font-family: $rosendale;
    font-size: 78rem;
  }
  @include full {
    font-size: 82px;
  }
  @include mobile {
    font-size: 43rem;
    em {
      font-size: 40rem;
    }
    .motion-txt.txt-hello .txt-target {
      display: block;
    }
  }
}

// 테크 섹션 디스크립션 셋
%tech-desc-set {
  margin-top: 110rem;
  font-size: 46rem;
  font-family: $notosanskrm;
  line-height: 1.43;
  small {
    vertical-align: top;
    font-family: $ambit-semi;
    font-size: 22rem;
    line-height: 1.63;
  }
  @include full {
    margin-top: 110px;
    font-size: 46px;
    small {
      font-size: 22px;
    }
  }
  @include mobile {
    margin-top: 45rem;
    font-size: 23rem;
    small {
      font-size: 11rem;
    }
  }
}

// 테크 섹션 서브 디스크립션 셋
%tech-desc-sub-set {
  margin-top: 48rem;
  font-size: 24rem;
  line-height: 1.58;
  font-family: $notosanskrm;
  @include full {
    margin-top: 48px;
    font-size: 24px;
  }
  @include mobile {
    font-size: 16rem;
  }
}

.motion-txt {
  @include text-effect('.txt-target');
}

.img-box {
	@include img-effect;
}

.p-tech {
  &.loading {
		height: 100vh;
		overflow: hidden;
  }
  transition: $black-color-transition;
  &.black {
    background: #000;
    color: #fff;
    transition: $black-color-transition;
    .newtech {
      background: #000;
      transition: $black-color-transition;
    }
    .newtech__images__img--01 {
      transition: $black-color-transition;
      background-image: url('/images/img_tech_newtech_01-white.png');
    }
    .technology {
      background-color: #000;
      transition: $black-color-transition;
    }
  }
}



.section__inner {
  position: relative;
  max-width: 1920px;
  height: 100%;
  margin: 0 auto;
  padding: 0 #{$pc-side-pad}rem 0;
  @include mobile {
    padding: 0 #{$mo-side-pad} 0;
  }
}


.keyvisual {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #fff;
  @media screen and (max-height:1024px) and ( min-width: 769px) {
    height: 100vh;
    .keyvisual__int {
      height: 100vh;
      @include mobile {
        height: 318rem;
      }
      .keyvisual__img {
        width: 100%;
        height: 100%;
      }
      .keyvisual__img-wrap {
        height: 56vh;
      }
      .keyvisual__desc {
        top: calc(50% + 28vh + 45rem);
      }
    }
  }

  &__cross-title {
    position: absolute;
    top: calc(50% - 69rem);
    left: 50%;
    z-index: 5;
    transform: translate(-50%, -50%);
    font-size: 120rem;
    line-height: 1;
    font-family: $ambit-semi;
    @include full {
      font-size: 120px;
    }
    @include mobile {
      font-size: 68rem;
    }
    @media screen and (min-width:390px) and (max-width: 499px) {
      top: calc(50% - 35rem);
    }
    .keyvisual__title {
      display: block;
      position: relative;
      left: 50%;
    }

    @mixin text-transform($number, $start) {
      $transition : all 1.7s cubic-bezier(.15,.85,.45,1);

      .keyvisual__title--0#{$number} {
        transform: translate(#{$start}rem, 0);
        transition: $transition;
        @include full {
          transform: translate(#{$start}px, 0);
        }
        @at-root .keyvisual__cross-title--loaded {
          .keyvisual__title--0#{$number} {
            text-align: center;
            transform: translate(-50%, 0);
            transition: $transition;
          }
        }
      }
    }

    @include text-transform(1, -461);
    @include text-transform(2, 190);


    @include mobile {
      @include text-transform(1, -205);
      @include text-transform(2, 41);
    }


    em {
      font-family: $rosendale;
      font-size: 112rem;
      @include full {
        font-size: 112px;
      }
      @include mobile {
        font-size: 68rem;
      }
    }
  }
  &__int {
    position: relative;
    width: 100%;
    height: 100vh;

  }
  &__img-wrap {
    position: absolute;
    top: calc(50% - 60rem);
    left: 50%;
    transform: translate(-50%, -50%);
    width: 500rem;
    height: 680rem;
    overflow: hidden;

    max-width: #{$kv-width}rem;
    max-height: #{$kv-height}rem;
    margin: 0 auto;
    @include mobile {
      width: 235rem;
      height: 349rem;
    }
    @media screen and (min-width:390px) and (max-width: 499px) {
      top: calc(50% - 10rem);
    }
  }
  &__img {
    position: relative;
    width: 500rem;
    height: 680rem;

    @include full {
      width: 500px;
      height: 680px;
      // height: 780px;
    }
    @include mobile {
      width: 234rem;
      height: 318rem;
    }
    &__list {
      position: absolute;
      top: 0;
      left: 50%;
      width: 100%;
      height: 100%;
      transform: translate(-50%, 0);
      visibility: hidden;
      &--01 figure{
        background-image: url('/images/img_tech_kv_01.png');
      }
      &--02 figure{
        background-image: url('/images/img_tech_kv_02.png');
      }
      &--03 figure{
        background-image: url('/images/img_tech_kv_03.png');
      }
      figure {
        display: block;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
      }
    }
  }
  &__desc {
    position: absolute;
    top: calc(50% + 340rem + 60rem);
    bottom: auto;
    left: 50%;
    transform: translate(-50%,-50%);
    font-family: $notosanskrm;
    font-size: 28rem;
    text-align: center;
    line-height: 1.5;
    @include text-effect('p .txt-target');
    @include full {
      font-size: 28px;
    }
    @include mobile {
      width: 100%;
      top: calc(50% + 159rem);
      font-size: 16rem;
    }
    @media screen and (min-width:390px) and (max-width: 499px) {
      font-size: 13rem;
      top: calc(50% + 190rem);
    }
  }
}


.newtech {
  padding: 75rem 0 100rem 0;
  background-color: #fff;
  transition: $black-color-transition;
  @include full {
    padding: 75px 0 100px 0;
  }
  @include mobile {
    padding: 30rem 0 0 0;
  }

  &__image {
    width: 100%;
    height: 75.1vh;
    background: url('/images/img_tech_heading.jpg') no-repeat center / cover;
  }

  &__contents {
    // display: flex;
    margin-top: 110rem;
    @include full {
      margin-top: 110px;
    }
    @include mobile {
      margin-top: 50rem;
      display: block;
    }
    &__title {
      @extend %tech-title-set;
      @include text-effect('.txt-target');
    }
    &__desc {
      @extend %tech-desc-set;
      @include text-effect('p .txt-target');
    }
    &__desc-sub {
      @extend %tech-desc-sub-set;
    }
  }

  &__images {
    position: relative;
    margin-top: 48rem;
    &__inner {
      display: flex;
      align-items: flex-end;
      @include mobile {
        display: block;
      }
    }
    &__img {
      &--01 {
        width: 378rem;
        height: 286rem;
        background: url('/images/img_tech_newtech_01.png') no-repeat center / cover;
        transition: $black-color-transition;
        @include full {
          width: 378px;
          height: 286px;
        }
        @include mobile {
          width: 115rem;
          height: 88rem;
          margin-left: auto;
        }
      }
      &--02 {
        width: 68.18%;
        height: 692rem;
        margin-left: auto;
        @include full {
          width: 1080px;
          height: 692px;
        }

        @include mobile {
          width: 100vw;
          height: auto;
          margin: 50rem 0 0 -#{$mo-side-pad};
        }
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  .floorinfo {
    font-family: $ambit-semi;
    margin-top: 2rem;
    font-size: 24rem;
    line-height: 1.3;
    @include full {
      margin-top: 2px;
      font-size: 24px;
    }
    @include mobile {
      margin-top: 8rem;
      font-size: 16rem;
      line-height: 1.5;
    }
    .txt-target {
      opacity: 0;
    }
    &.txt-hello {
      .txt-target {
        opacity: 1;
      }
    }
  }
}

.technology {
  position: relative;
  padding-top: 140rem;
  background-color: #fff;
  transition: $black-color-transition;

  @include full {
    padding-top: 140px;
  }
  @include mobile {
    padding-top: 100rem;
    overflow: hidden;
  }
  &__title {
    @extend %tech-title-set;
    @include mobile {
      .motion-txt.txt-hello .txt-target {
        display: inline-block;
      }
    }
  }
  &__desc {
    @include text-effect('section .txt-target');
    position: relative;
    overflow: visible;
    z-index: 1;
    @extend %tech-desc-set;
    @include full {
      font-size: 64px;
    }
    .cheesetext {
      .cheese {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        figure {
          width: 100%;
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
        }
      }
      .cheese-bg {
        position: absolute;
        top: 440rem;
        z-index: 1;
        visibility: hidden;
        @include mobile {
          display: none;
        }
        figure {
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
        }
      }
      &:hover {
        .cheese {
          transition: width 0.5s ease-out;
        }
      }

      @mixin cheese ($idx, $width, $height, $top, $bg-width:0, $bg-height:0, $bg-top: 0, $bg-right: 0) {
        &--0#{$idx} {
          .cheese {
            top: $top;
            width: $width;
            margin-top: -#{$top};
            @include mobile {
              width: $width;
            }
            @at-root .cheesetext--0#{$idx}:hover {
              .cheese-bg {
                visibility: visible;
                top: $bg-top;
                right: $bg-right;
              }
            }
            figure {
              height: $height;
              background-image: url('/images/img_tech_cheese_0#{$idx}.png');
            }
            &-bg {
              figure {
                width: $bg-width;
                height: $bg-height;
                background-image: url('/images/img_tech_cheese_bg_0#{$idx}.png');
              }
            }
          }
        }
      }

      //            idx   width   height  top  bgwidth  bgheight  bgtop bgright
      @include cheese (1, 112rem, 64rem, 0rem, 561rem, 441rem, 370rem, 107rem);
      @include cheese (2, 208rem, 47rem, 0rem, 936rem, 343rem, 343rem, -80rem);
      @include cheese (3, 40rem, 70rem, 5rem, 381rem, 541rem, 53rem, 108rem);

      @include mobile {
        @include cheese (1, 60rem, 33rem, 0rem);
        @include cheese (2, 93rem, 24rem, 0rem);
        @include cheese (3, 26rem, 40rem, 0rem);
      }

    }
  }
  &__desc-sub {
    @extend %tech-desc-sub-set;
  }
}

.catchphares {
  display: inline-block;
  position: relative;
	display: block;
  max-width: 1920px;
  margin: 290rem auto 0;
  padding-left: #{$pc-side-pad}rem;
	text-transform: uppercase;
  font-family: $ambit-semi;
  @include full {
    margin-top: 290px;
    padding-left: #{$pc-side-pad}px;
  }
  @include mobile {
    width: calc(100% + #{$mo-side-pad});
    overflow: hidden;
    margin-top: 127rem;
    padding: 0 0 30rem 0;
  }
  &__title {
		display: flex;
    font-size: 240rem;
		justify-content: flex-start;
    @include mobile {
      position: relative;
      font-size: 48rem;
    }
    @include pc {
      font-size: 190rem;
    }
    @include tablet {
      font-size: 80rem;
      padding-left: #{$mo-side-pad};
    }
    @include full {
      margin-top: 240px;
    }
    @include ie-only {
      line-height: 1.1;
      font-size: 230rem;
    }
		span {
			display: inline-block;
			vertical-align: top;
    }
    em {
      text-transform: none;
      font-family: $rosendale;
      font-size: 230rem;
      @include ie-only {
        line-height: 0.8;
        font-size: 220rem;
      }
      @include pc {
        font-size: 190rem;
      }
      @include mobile {
        font-size: 50rem;
      }
      @include tablet {
        font-size: 80rem;
      }
    }
	}
}