$c--p1: #222;

$full-width:1920px;
$pc-maxwidth:1200px;
$pc-minwidth:769px;
$tablet-maxwidth:768px;
$mobile-width:500px;
@mixin full {
    @media all and (min-width: #{$full-width}) {
        @content;
    }
}
@mixin pc {
    @media all and (min-width:#{$pc-minwidth}) and (max-width:#{$pc-maxwidth}) {
        @content;
    }
}
@mixin mobile {
    @media all and (max-width:#{$tablet-maxwidth}) {
        @content;
    }
}
@mixin tablet {
    @media all and (min-width:#{$mobile-width}) and (max-width:#{$tablet-maxwidth}) {
        @content;
    }
}
@mixin ie-only {
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        @content;
    }
}

// 멀티라인 말줄임 표시
// $line-cnt : 라인 수
// $line-height : line-height값
// 사용법 : @include ellipsis(3, 1.6em);
@mixin ellipsis($line-cnt, $line-height) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: $line-cnt; /* 라인수 */
    -webkit-box-orient: vertical;
    word-wrap:break-word;
    line-height: $line-height;
    max-height: $line-height * $line-cnt;
}
@mixin text-effect($targetTxt){
  overflow: hidden;
  visibility: hidden;
  &.motion-txt--ko {
    #{$targetTxt} {
      display: block;
      opacity: 0;
      -ms-transition: transform 1.5s cubic-bezier(.15,.85,.45,1),opacity 1.5s cubic-bezier(.32,.94,.6,1);
      transition: transform 1.5s cubic-bezier(.15,.85,.45,1),opacity 1.5s cubic-bezier(.32,.94,.6,1);
      visibility: hidden;
      // &:nth-child(2) {
      //   transition-delay: 0.2s;
      // }
      // &:nth-child(3) {
      //   transition-delay: 0.3s;
      // }
      // &:nth-child(4) {
      //   transition-delay: 0.4s;
      // }
      // &:nth-child(5) {
      //   transition-delay: 0.4s;
      // }
    }
  }
  #{$targetTxt} {
    display: inline-block;
    transform: translate(0,100%);
    -ms-transition: opacity 0.7s cubic-bezier(.19,1,.22,1),transform 0.7s cubic-bezier(.19,1,.22,1);
    transition: opacity 0.7s cubic-bezier(.19,1,.22,1),transform 0.7s cubic-bezier(.19,1,.22,1);
  }
  &.txt-hello {
    visibility: visible;
    #{$targetTxt} {
      transform: translate(0,0) rotate(.001deg);
      -ms-transition: transform 1s cubic-bezier(.15,.85,.45,1),opacity 1s cubic-bezier(.32,.94,.6,1);
      transition: transform 1s cubic-bezier(.15,.85,.45,1),opacity 1s cubic-bezier(.32,.94,.6,1);
      &:nth-child(2) {
        transition-delay: 0.2s;
      }
      &:nth-child(3) {
        transition-delay: 0.3s;
      }
      &:nth-child(4) {
        transition-delay: 0.4s;
      }
      &:nth-child(5) {
        transition-delay: 0.5s;
      }
      &:nth-child(6) {
        transition-delay: 0.6s;
      }
      &:nth-child(7) {
        transition-delay: 0.7s;
      }
      &:nth-child(8) {
        transition-delay: 0.8s;
      }
    }
    &.motion-txt--ko {
      #{$targetTxt} {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
@mixin text-effect-type2($targetTxt){
  overflow: hidden;
  visibility: hidden;
  &.motion-txt--ko {
    #{$targetTxt} {
      display: block;
      opacity: 0;
      -ms-transition: transform 2s cubic-bezier(.15,.85,.45,1),opacity 2s cubic-bezier(.32,.94,.6,1);
      transition: transform 2s cubic-bezier(.15,.85,.45,1),opacity 2s cubic-bezier(.32,.94,.6,1);
      visibility: hidden;
    }
  }
  #{$targetTxt} {
    display: inline-block;
    transform: translate(0,100%);
    -ms-transition: opacity 1s cubic-bezier(.19,1,.22,1),transform 1s cubic-bezier(.19,1,.22,1);
    transition: opacity 1s cubic-bezier(.19,1,.22,1),transform 1s cubic-bezier(.19,1,.22,1);
  }
  &.txt-hello {
    visibility: visible;
    #{$targetTxt} {
      transform: translate(0,0) rotate(.001deg);
      -ms-transition: transform 2s cubic-bezier(.15,.85,.45,1),opacity 2s cubic-bezier(.32,.94,.6,1);
      transition: transform 2s cubic-bezier(.15,.85,.45,1),opacity 2s cubic-bezier(.32,.94,.6,1);
      &:nth-child(2) {
        transition-delay: 0.5s;
      }
      &:nth-child(3) {
        transition-delay: 1s;
      }
      &:nth-child(4) {
        transition-delay: 1.5s;
      }
      &:nth-child(5) {
        transition-delay: 2s;
      }
      &:nth-child(6) {
        transition-delay: 2.5s;
      }
      &:nth-child(7) {
        transition-delay: 3s;
      }
    }
    &.motion-txt--ko {
      #{$targetTxt} {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
@mixin img-effect {
  position: relative;
  opacity: 0;
  transition: opacity 1.3s cubic-bezier(.19,1,.22,1),transform 2s cubic-bezier(.19,1,.22,1);
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgb(240, 228, 237); //디자인받은 컬러 추가 (20210128 고객사 피드백 컬러 반영)
    transition: height 1s .1s cubic-bezier(.19,1,.22,1);
  }
  &.img-hello {
    opacity: 1;
    &:after {
      height: 0;
    }
  }
}
